body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI',  'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #AAA;
  letter-spacing: 1px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.game {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.game-info {
  font-size: 30px;
  font-weight: 700;
  text-align: center;

  margin-bottom: 50px;
}

.game-info span {
  font-size: 40px;
  color: #FFF;
}

.board-row {
  display: flex;
}

.square {
  width: 100px;
  height: 100px;
  margin: 2px;

  background: #FFFFFF;
  background: -webkit-linear-gradient(top left, #FFFFFF, rgb(196, 196, 196));
  background: -moz-linear-gradient(top left, #FFFFFF, rgb(196, 196, 196));
  background: linear-gradient(to bottom right, #FFFFFF, rgb(196, 196, 196));
  box-shadow: 0 0 10px #333;
  border-radius: 5px;

  font-size: 100px;
  line-height: .9em;
  text-align: center;

  transition: .5s;
}

.square:hover {
  background: #fff;
}

.square:active {
  transform: scale(.9);
  transition: .2s;
}

.menu {
  margin-top: 50px;
}

.button {
  width: 150px;
  height: 40px;
  margin: 5px 5px;

  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  background-color: #333;
  color: #fff;
  border: 0;
  border-radius: 5px;
  outline: none;

  transition: .5s;
}

.button:hover {
  box-shadow: 0 0 20px #fff;
  background-color: #fff;
  color: #000;
}

.button:active {
  transform: scale(.9);
  box-shadow: 0 0 50px #fff;
  transition: .2s;
}

@media (max-width : 450px) {
  .game-info {
    margin-bottom: 10px;
  }

  .menu {
    display: flex;
    flex-direction: column;
    margin-top: 5px;
  }
}